export const setVersionIE = (state) => {
    let userAgent = window.navigator.userAgent;
    let msie = userAgent.indexOf('MSIE ');
    let trident = userAgent.indexOf('Trident/');
    let edge = userAgent.indexOf('Edge/');

    if (msie > 0) {
        // IE 10 or older => return version number
        state.versionIE =  parseInt(userAgent.substring(msie + 5, userAgent.indexOf('.', msie)), 10);
    }


    else if (trident > 0) {
        // IE 11 => return version number
        let rv = userAgent.indexOf('rv:');
        state.versionIE = parseInt(userAgent.substring(rv + 3, userAgent.indexOf('.', rv)), 10);
    }


    else if (edge > 0) {
        // Edge (IE 12+) => return version number
        state.versionIE = parseInt(userAgent.substring(edge + 5, userAgent.indexOf('.', edge)), 10);
    }

    // other browser
    else state.versionIE = 'false';
};




